@keyframes swell {
  from {
    width: 5px;
    height: 5px;
  }
  to {
    width: 200px;
    height: 200px;
  }
}

@keyframes contract {
  from {
    width: 200px;
    height: 200px;
  }
  to {
    width: 5px;
    height: 5px;
  }
}

/*
*   App Styles
*/

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
  overflow: hidden;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: stretch;
  padding: 16px;
  width: 100%;
  height: 100vh;
  /* position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0; */
}

.breath-msg h3 {
  font-size: 28px;
  font-weight: 400;
  color: rgb(0, 64, 215);
  line-height: 50px;
}

.breath-count h3 {
  font-size: 24px;
  font-weight: 400;
  color: rgb(0, 64, 215);
  line-height: 50px;
}

.breath-controls {
  min-height: 25%;
  width: 80%;
  max-width: 400px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: stretch;
}

.breath-controls div span {
  line-height: 50px;
  font-size: 14px;
  font-weight: 400;
  color: rgb(0, 64, 215);
}

.ball-parent {
  width: 100%;
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.ball {
  border-radius: 50%;
  background-color: rgb(0, 64, 215);
  box-shadow: 0px 0px 30px 20px rgb(0, 64, 215);
}

#playButton {
  border: none;
  background: none;
}

#playButton i {
  font-size: 30px;
  color: rgb(0, 64, 215);
  display: inline-block;
}

#stopButton {
  border: none;
  background: none;
}

#stopButton i {
  font-size: 30px;
  color: rgb(0, 64, 215);
  display: inline-block;
}

input[type="range"] {
  -webkit-appearance: none;
  width: 100%;
  background: linear-gradient(90deg, rgba(233,29,3,1) 0%, rgba(0,212,255,1) 100%);
}

input[type="range"]:focus {
  outline: none;
}

input[type="range"]::-ms-track {
  width: 100%;
  cursor: pointer;
  background: transparent;
  border-color: transparent;
  color: transparent;
}

input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  /* custom slider thumb styles */
  height: 16px;
  width: 16px;
  border-radius: 50%;
  background: rgb(0, 64, 215);
  cursor: pointer;
}

input[type="range"]::-webkit-slider-runnable-track {
  /* custom slider track styles */
  width: 100%;
  cursor: pointer;
  border: 1px solid rgb(0, 64, 215);
  border-radius: 5px;
  padding: 4px 0;
}
